import {User, userRoles} from './User'
import Cookies from 'js-cookie';

let current = {};

// Пока не нужно
let countdown = 0.0;

export const defaultSession = {
    user: new User(),
    validThrough: new Date("Jan 1, 2077 00:00:00"),
    authToken: '',

    // Это тоже пока не используем
    refreshToken: '',
}

export class Session {
    static startNew(overrides = {}) {
        current = {};

        current = Object.assign(current, defaultSession);
        current = Object.assign(current, overrides);
    }

    // Пока что не пользуемся куками
    static start() {
        
    }

    static edit(changes = {}) {
        Object.assign(current, changes);
    }

    static getCurrent() {
        // Выдаём копию, чтобы ненароком ничего не менять в самой сессии
        return Session.import(Session.export());
    }

    static export() {
        return JSON.stringify(current);
    }

    static import(JSONstring) {
        return JSON.parse(JSONstring, function(key, value) {
            if (key == 'user') {
                return new User(JSON.parse(value));
             }
            
            if (key == 'validThrough') return new Date(value);
            return value;
        });
    }

    // Пока что не нужно
    static writeCookie() {
        Cookies.set('session', JSON.stringify(current), { expires: 1, secure: true })
    }

    // И это тоже
    static readCookie() {
        return JSON.parse((Cookies.get('session')));
    }
}

