import {inputNumber, confirmNumber} from "./api/fakeApi"
import {inputCard, logout, requestService} from "./api/realApi"
import { Session } from "./user/Session"

import {ACTION, status} from './constants/dummyConstants'
/**
 * REDUCE ОБРАБАТЫВАЕТ ВСЕ ВХОДЯЩИЕ ЗАПРОСЫ С ФОРМ \
 * ПРИНИМАЕТ ЗАПРОС, ИСПОЛЬЗУЕТ СООТВЕТСТВУЮЩЕЕ ДЕЙСТВИЕ \
 * 
 * @param {*} action действие, которое мы хотим обработать
 * @returns ответ в виде строки-константы STATUS (см. dummyConstants), например 'SUCCESS' или 'FAIL'
 */
async function reduce(action) {
    switch (action.type) {
        case ACTION.READ_CARD:
            return inputCard(Session.getCurrent(), action.data)
        case ACTION.LOGIN:
            return inputNumber(Session.getCurrent(), action.data)
        case ACTION.CONFIRM:
            return confirmNumber(Session.getCurrent(), action.data)
        case ACTION.LOGOUT:
            return logout(Session.getCurrent(), action.data)
        case ACTION.REQSERVICE:
            return requestService(Session.getCurrent(), action.data)
        default:
            return Promise.reject(status.NOT_FOUND);
        }
}

export default reduce;