import React from 'react';
import ServicesMenu from '../Services/ServicesMenu';
import MapDetail from './MapDetail';
import AutoMenu from '../Menus/AutoMenu';

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect,
  }
from 'react-router-dom';

export default class Maps extends React.Component {

    render() {
        return (
            <Switch>
            <Route path='/maps/:category' render={(props) => 
                {
                    return <MapDetail category={props.match.params.category} {...props}/>
                }
            }/>

            <Route exact path="/maps" render={(props) => 
                <>
                <div className="container mx-auto text-center">
        
                    {/* Контейнер с кнопками */}
                    {console.log(this.props.location.pathname)}
                    <div className="grid grid-cols-2 gap-4 py-10 px-4">
                    <AutoMenu url={this.props.location.pathname} {...props}/>
                    </div>
                </div>
                </>} />

            <Redirect to="/" />
            </Switch>
        )
    }
}