export const userRoles = {
    GUEST: 'GUEST',
    NO_PHONE: 'NO_PHONE',
    INPUT_PHONE: 'INPUT_PHONE',
    UNCONFIRMED: 'UNCONFIRMED',
    EMPLOYEE: 'EMPLOYEE'
}

// Нельзя использовать set, потому что он не переносит JSON.Stringify
export const defaultUser = {
    name: 'Гость',
    phoneNumber: '+70000000000',
    roles: new Set([userRoles.GUEST]),
}

export class User {
    /**
     * Посмотрите на структуру объекта пользователя \
     * Время жизни сессии (expires) пишется в секундах \
     * Подразумевается, что данные будут храниться на стороне клиента, \
     * чтобы не делать слишком много запросов к апи \
     * Но апи должен хранить копию этих данных, чтобы проверять их целостность по токену \
     * как минмум при отправке формы \
     * Потому что иначе любой с шаловливыми ручками может залезть в дебаггер или куки \
     * И поменять там значения, на какие ему нужно
     */

    constructor(overrides = {}) {
        Object.assign(this, defaultUser);
        Object.assign(this, overrides);
        
        if (!(typeof this.roles === 'object' && this.roles instanceof Set)) {
            this.roles = new Set(this.roles);
        }
        return this;
    }


    getFullName() {
        const result = {
            lastName: '',
            firstName: '',
            middleName: '',
        }

        let array = this.name.split(' ');

        let i = 0;
        Object.keys(result).forEach(key => {
            if (i < array.length)
            result[key] = array[i];
            ++i;
        });

        return result;
    }

    // Кастомное поведение при JSON.stringify
    // Нужно для преобразования сета в массив, иначе оно записывается как {}
    toJSON() {
        return JSON.stringify ({
            name: this.name,
            phoneNumber: this.phoneNumber,
            roles: [...this.roles],
        })
    }

    clone(overrides = {}) {
        return new User(Object.assign(this.getInfo(), overrides));
    }
}