import React from 'react';
import { Document, Page } from 'react-pdf/dist/esm/entry.webpack';

import ContentButton from '../Buttons/ContentButton';

import LoaderScreen from '../LoaderScreen'
import { getNewsList } from '../../scripts/api/contentApi';
//import '../../assets/css/scrollbar.css'

const gutter = 48;
export default class News extends React.Component {

    constructor(props) {
        super(props)
        this.state = {numPages: 1, pageNumber: 1, 
        window: {width: window.innerWidth, height: window.innerHeight},
        loading: true}
    }

    componentDidMount() {
        getNewsList().then(result => {
            this.setState({list: result, current: 0, loading: false})
        })
        .catch(error => {
            this.setState({loading: false})
        })
    }

    previousEnty() {
        this.changeEntry.call(this, 1);
    }

    nextEntry() {
        this.changeEntry.call(this, -1);
    }

    changeEntry(offset) {
        this.setState({pageNumber: 1, current: this.state.current + offset})
    }


    onDocumentLoadSuccess({ numPages }) {
        this.setState({numPages: numPages, pageNumber: 1});
    }

    changePage(offset) {
        this.setState({pageNumber: this.state.pageNumber + offset})
    }

    previousPage() {
        this.changePage.call(this, -1);
    }

    nextPage() {
        this.changePage.call(this, 1);
    }

    render() {

        if (this.state.loading) {
            return <LoaderScreen/>
        }

        else
        return (
        <div class="bg-gray-900 pb-24 pt-24 relative">
            <Document
                file={this.state.list[this.state.current].url}
                renderMode={'svg'}
                onLoadSuccess={this.onDocumentLoadSuccess.bind(this)}
            >
                <Page 
                width={this.state.window.width - gutter*2}
                className={'flex justify-center select-none'}
                pageNumber={this.state.pageNumber} />
            </Document>
            <div class="fixed top-0 left-0 right-0 flex items-center h-24 bg-gray-900">
                <div class="text-white px-8 gap-8 flex justify-center items-center w-full relative">
                    <div class={`justify-start items-center absolute left-10`}>
                        <ContentButton
                            onClick={(e) => {
                                e.preventDefault();
                                this.props.history.push("/")
                            }}
                            direction="none"
                            className="justify-start bg-red hover:bg-blue-lighter"
                        >
                            Выход
                        </ContentButton>
                    </div>
                    
                    <ContentButton
                        disabled={this.state.current === this.state.list.length - 1}
                        onClick={(e) => {
                            e.preventDefault();
                            this.previousEnty.call(this)
                        }}
                        direction="left"
                    >
                    Предыдущий выпуск
                    </ContentButton>

                    <p class="text-3xl font-black">
                        {this.state.list[this.state.current].title}
                    </p>

                    <ContentButton
                        disabled={this.state.current === 0}
                        onClick={(e) => {
                            e.preventDefault();
                            this.nextEntry.call(this)
                        }}
                        direction="right"
                    >
                    Следующий выпуск
                    </ContentButton>

                </div>
            </div>

            <div class="fixed bottom-0 left-0 right-0 flex items-center h-24 bg-gray-900">
                <div class="text-white px-8 gap-8 w-full flex justify-center items-center">

                    <ContentButton
                        disabled={this.state.pageNumber <= 1}
                        onClick={(e) => {
                            e.preventDefault();
                            this.previousPage.call(this)
                        }}
                        direction="left"
                    >
                        Назад
                    </ContentButton>

                    <p class="text-3xl">
                    Страница {this.state.pageNumber || (this.state.numPages ? 1 : '--')} из {this.state.numPages || '--'}
                    </p>

                    <ContentButton
                        disabled={this.state.pageNumber >= this.state.numPages}
                        onClick={(e) => {
                            e.preventDefault();
                            this.nextPage.call(this)
                        }}
                        direction="right"
                    >
                    Вперёд
                    </ContentButton>
                </div>
            </div>
        </div>
        )
    }
}