import React from 'react';
import PropTypes from 'prop-types'
import MainButton from '../Buttons/MainButton';
import TimeoutButtonOutline from '../Buttons/TimeoutButtonOutline';

const directions = ["left", "bottom", "top", "right"];

export default class ToolbarSecondary extends React.Component {

    static propTypes = {
        /** 
         * Массив кнопок, которые нужно отобразить.
         * @default []
         * @example
         *      [{text: "На главную",
         *      onClick: (e) => {
         *      e.preventDefault();
         *      this.props.history.push("/")
         *      }}]
         */
        buttons: PropTypes.array,

        /**
         * Размер. Один из ['sm', 'lg']
         * @default 'sm'
         */
        size: PropTypes.string,
        }

    static defaultProps = {
        buttons: [],
        size: 'sm',
    }

    render() {
        let n = this.props.buttons.length;
        let buttons = this.props.buttons;

        return (
            <div className={`flex justify-start items-center flex-wrap -ml-5`}>
            {
                buttons.map((button,index) => {
                    return (
                    <div className="ml-5">
                        <TimeoutButtonOutline
                        size={this.props.size}
                        timeout={button.timeout}
                        onClick={button.onClick}
                        color={button.color}>
                            {button.text}
                        </TimeoutButtonOutline>
                    </div>
                    )
                })
            }
            </div>
        );
    }
}