import React from 'react';
import PropTypes from 'prop-types'

export default class ContentButton extends React.Component {
    static propTypes = {

        /** 
         * "Направление" кнопки. Пока что влияет только на стрелочку \
         * Может быть left, right или никаким
         * @default "left"
         */
        direction: PropTypes.string,

        /** 
         * Действие при нажатии кнопки
         */
        onClick: PropTypes.func,

        /** 
         * Добавочные css классы к кнопке \
         * желательно использовать классы Tailwind Css
         * @example className = "col-span-2 row-span-1" 
         * растянет кнопку внутри сетки (CSS) на 1 ряд и 2 колонки 
         * @example className = "bg-blue-lighter hover:bg-red"
         * сделает кнопку синей, а при наведении на неё мышкой - красной 
         */
        className: PropTypes.string,

        /**
         * Выключена ли кнопка
         */
        disabled: PropTypes.any,

        /** 
         * Внутри компонента пишется текст кнопки
         */
        children: PropTypes.oneOfType([PropTypes.number, PropTypes.string])
    }

    static defaultProps = {
        size: 'md',
        direction: 'left',
        children: 'Кнопка',
        disabled: false,
    }

    render() {
        switch (this.props.direction) {
            case 'left':
                return (
                <button type="button"
                onClick={this.props.onClick}
                disabled={this.props.disabled}
                class={`bg-gray-800 text-white rounded-l-md
                py-3 hover:bg-red
                transition duration-150 ease-out
                hover:text-white px-5 ${this.props.className}`}>
                <div class="flex flex-row align-middle items-center">
                    <svg class="w-8 mr-4" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" d="M7.707 14.707a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 1.414L5.414 9H17a1 1 0 110 2H5.414l2.293 2.293a1 1 0 010 1.414z" clip-rule="evenodd"></path>
                    </svg>
                    <p class="ml-4 text-xl uppercase font-black">{this.props.children}</p>
                </div>
                </button>
            )

            case 'right':
                return (
                <button type="button"
                onClick={this.props.onClick}
                disabled={this.props.disabled}
                class={`bg-gray-800 text-white rounded-r-md
                transition duration-150 ease-out
                py-3 hover:bg-red
                hover:text-white px-5 ${this.props.className}`}>
                    <div class="flex flex-row align-middle items-center">
                    <p class="mr-4 text-xl uppercase font-black">{this.props.children}</p>
                    <svg class="w-8 mr-4" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
                        <path fill-rule="evenodd" d="M12.293 5.293a1 1 0 011.414 0l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414-1.414L14.586 11H3a1 1 0 110-2h11.586l-2.293-2.293a1 1 0 010-1.414z" clip-rule="evenodd"></path>
                    </svg>
                    </div>
                </button>
                )
            
            default:
                return (
                    <button type="button"
                    onClick={this.props.onClick}
                    disabled={this.props.disabled}
                    class={`bg-gray-800 text-white rounded-md
                    py-3 hover:bg-red
                    transition duration-150 ease-out
                    hover:text-white px-5 ${this.props.className}`}>
                        <div class="flex flex-row align-middle items-center">
                        <p class="mx-2 text-xl uppercase font-black">{this.props.children}</p>
                        </div>
                    </button>
                )
        }

    }
}