const DUMMY_PHONE = '+75555555555';
const DUMMY_CONFIRM_NUMBER = '55555';
const DUMMY_USER_NAME = 'Иванов Иван Иванович'
const DUMMY_TOKEN = 'ffff0000';
const DUMMY_REFRESH_TOKEN = '0000ffff'
const DUMMY_CARD_NUMBER = '1362859'

