import React from 'react';
import PropTypes from 'prop-types'
import MainButton from '../Buttons/MainButton';

const directions = ["left", "bottom", "top", "right"];

export default class ToolbarMain extends React.Component {

    static propTypes = {
        /** 
         * Массив кнопок, которые нужно отобразить.
         * @default []
         * @example
         *      [{text: "На главную",
         *      onClick: (e) => {
         *      e.preventDefault();
         *      this.props.history.push("/")
         *      }}]
         */
        buttons: PropTypes.array,
    }

    static defaultProps = {
        buttons: [],
    }

    render() {
        let n = this.props.buttons.length;
        let buttons = this.props.buttons;

        return (
            <div className={`grid h-56 grid-cols-${n > 1 ? n : 2} grid-rows-1 gap-4 py-10 absolute bottom-0 left-0 right-0`}>
            {
                buttons.map((button,index) => {
                    return <MainButton className="col-span-1 row-span-1 bg-blue-lighter hover:bg-red"
                    size="lg"
                    direction={n === 1
                        ? directions[0]
                        : index === n - 1
                            ? directions[directions.length - 1]
                            : directions[index % directions.length]}
                    onClick={button.onClick}>
                        {button.text}
                    </MainButton> 
                })
            }
            </div>
        );
    }
}