import React from 'react';
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom';

/**
 * Кнопка на главной странице
 */
export default class SecondaryButton extends React.Component {
    static propTypes = {
        /** 
         * "Направление" кнопки. Влияет на границу
         * Может быть left, top, right или bottom
         * @default "left"
         */
        direction: PropTypes.string,

        /** 
         * Иконка кнопки, url
         */
        icon: PropTypes.string,

        /** 
         * Ссылка, по которой ведёт кнопка \
         * Если задана, кнопка оборачивается в ссылку и функция OnClick не срабатывает \
         */
        link: PropTypes.string,

        /** 
         * Дополнительный текст к кнопке
         */
        description: PropTypes.string,

        /** 
         * Добавочные css классы к кнопке \
         * желательно использовать классы Tailwind Css
         * @example className = "col-span-2 row-span-1" 
         * растянет кнопку внутри сетки (CSS) на 1 ряд и 2 колонки 
         */
        className: PropTypes.string,

        /** 
         * Функция, которая выполняется по нажатию на кнопку
         * @default () => {}
         */
        onClick: PropTypes.func,


        /** 
         * Внутри компонента пишется текст кнопки
         */
        children: PropTypes.oneOfType([PropTypes.number, PropTypes.string])
    }

    static defaultProps = {
        direction: 'left',
        onClick: (e) => {},
    }
    
    #arrowRotation = {
        left:  'left-0 rotate-0',
        right:  'right-0 rotate-180',
    }

    render() {
      const ParentTagName = this.props.link ? Link : 'button'
      return (
        <ParentTagName className={
            `${this.props.direction === 'left' ? 'bg-gradient-to-r' : 'bg-gradient-to-l'}
        from-blue-lighter to-red p-1 ` + this.props.className}
        onClick={this.props.onClick}
        to={this.props.link}
        >

        <div className="container 
        bg-white flex items-center content-center justify-between text-center relative h-40">
            <div className= {`
            ${this.props.direction === 'left' ? 'pl-16' : 'pl-6'}
            pt-7 pb-4 text-left`}>
            <h3 className={
                `text-4xl font-black
                ${this.props.direction === 'left' ? 'text-blue-darker' : 'text-red'}`}>
                {this.props.children}
            </h3>
            <p class="text-gray-500 mt-2">
                {this.props.description}
            </p>
            </div>
            <div className="p-4">
            <div className={
                `w-24 mx-auto
                ${this.props.direction === 'left' ? 'text-red' : 'text-blue-darker mr-7'}`}
                
            >
                { this.props.icon ? <img className='min-w-full' src={this.props.icon} alt="icon"/> : null}
            </div>
            </div>
            <div className={
                `w-12 h-12
                absolute
                text-blue-darker
                transform
                ${this.#arrowRotation[this.props.direction]}
                `}>
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
                </svg>
            </div>
        </div>
        </ParentTagName>
      );
    }
}