import React from 'react';
import MainButton from '../../Buttons/MainButton';
import PropTypes from 'prop-types'
import InputMask from 'react-input-mask';

import format from '../../../scripts/helperFunctions/format';
import '../../../assets/css/input-number-fix.css'
import '../../../assets/css/postcard.css'
import VirtualPinpad from '../../VirtualPinpad'

export default class ConfirmNumberForm extends React.Component {
    static propTypes = {
        /**
         *  Действие при изменении значения поля
         *  Предпологается, что код подтверждения отправляется, \
         *  только когда поле заполнено до конца. \
         *  См. использование в файле LoginForm.jsx
         *  @default (value, mask) => console.log(value)
         */
         onChange: PropTypes.func,

        /**
         *  Действие при отправки формы
         *  Тут нигде не используется
         *  @default (data) => {}
         */
        onSubmit: PropTypes.func,

        /**
         *  Номер телефона, который будет отображаться на форме
         *  (Без +7 или 8)
         *  @default '0000000000'
         */
        phoneNumber: PropTypes.oneOfType(PropTypes.number, PropTypes.string),

        /**
         *  Номер телефона, который будет отображаться на форме
         *  (Без +7 или 8)
         *  @default '00000'
         */
        numberMask: PropTypes.string
    }

    static defaultProps = {
        onChange: (value, mask) => console.log(value),
        phoneNumber: '+70000000000',
        numberMask: '99999'
    }

    componentDidMount() {
        this.searchInput.focus();
    }


    render() {
        // TODO:
        // Это костыль на первое время
        // Переписать обязательно
        //let redirectTo = this.props.location.search
        //.substr(this.props.location.search.indexOf('redirectTo=') + 'redirectTo='.length)

        return (
            <div className="container mx-auto text-center relative max-w-5xl">
            <div class="mx-auto">
            
            <form className="bg-white mt-10 left-0 right-0 shadow-md rounded px-8 mb-4">
                <div class="absolute -top-5 -left-5 -right-5 -bottom-5 -z-10 bg-postcard rounded shadow-md"></div>
                <h1 className="text-5xl pt-12 mx-auto text-blue-darker font-black">
                    Введите код подтверждения
                </h1>
                <p className="text-2xl pt-2 max-w-4xl mx-auto text-gray-500">
                    Мы уже выслали на номер {format(this.props.phoneNumber, '## (###) ### ##-## ')} код подтверждения
                    <br/>
                    Введите его в поле ниже, чтобы войти
                </p>
                <div className="mb-8 mt-2">

                <InputMask
                inputRef={inputEl => (this.searchInput = inputEl)}
                mask={this.props.numberMask}
                maskChar={'_'}
                onChange={(e) => this.props.onChange(e)}
                className="shadow appearance-none rounded w-72 py-2 px-3
                text-gray-500 text-center text-4xl font-black leading-tight tracking-widest focus:outline-none focus:shadow-outline"
                id="username" type="text" 
                //beforeMaskedValueChange={this.beforeMaskedValueChange}
                />

                <p className="text-center mt-6 text-gray-500 text-lg">
                &copy; 2021 РМ Рейл <br/>
                </p>
                </div>
            </form>
            
            </div>

            <VirtualPinpad onInput={(e) => this.searchInput.value += e}/>
            </div>
        );
    }
}