import React from 'react';
import PropTypes from 'prop-types'

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect,
  }
from 'react-router-dom';

import AskLoginForm from './Login/AskLoginForm';
import InputPhoneForm from './Login/InputPhoneForm';
import ConfirmForm from './Login/ConfirmNumberForm';
import LoaderScreen from '../LoaderScreen'

import reducer from '../../scripts/reducer'

import { userRoles } from '../../scripts/user/User';
import {Session} from '../../scripts/user/Session'
import { ACTION } from '../../scripts/constants/dummyConstants';
import TimeoutButtonOutline from '../Buttons/TimeoutButtonOutline';

// Время в секундах
const LOGOUT_T = 30;

export default class Forms extends React.Component {

    // Дописать действия при отправке формы (props.onSubmit)

    // TODO: сделать переходы в зависимости от success и fail
    constructor(props) {
        super(props);
        this.state = {loading: true, timer: LOGOUT_T}
    }

    componentDidMount() {
        this.setState({session: Session.getCurrent(), loading: false})
        //console.log(success)
        //console.log(fail)
    }

    componentWillMount() {
        
    }

    setLoading(value) {
        this.setState({loading: value})
    }

    updateSession(callback = () => {}) {
        this.setState({session: Session.getCurrent()}, callback)
    }


    onLogout(data) {
        let setLoading = this.setLoading.bind(this),
        updateSession = this.updateSession.bind(this);

        setLoading(true)
        
        return new Promise(function(resolve, reject) {
            reducer({type: ACTION.LOGOUT, data: data}).then((result) => {
                updateSession(setLoading(false));
                // ...
            },
            )
            .catch(result => {
                updateSession(setLoading(false));
                // ...
            })
        })
    }

    render() {
        if (this.state.loading)
            return <LoaderScreen/>
        else {
            const user = this.state.session.user;
            //console.log(this.state.session);
            //console.log(Session.getCurrent());

            if (user.roles.has(userRoles.EMPLOYEE))
                return (
                    <div class="absolute left-4 top-4 max-w-md flex flex-col space-y-4 justify-center items-center">
                    <div class="bg-transparent w-auto flex items-center p-2">
                    <div class="flex items-center space-x-4 text-red">
                        <svg xmlns="http://www.w3.org/2000/svg" class="h-16 w-16" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="1" d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                        </svg>
                    </div>
                    <div class="flex-grow p-3">
                    <div class="font-semibold text-xl text-blue-darker">
                        {user.getFullName().lastName + " " + user.getFullName().firstName}
                    </div>
                    {/* <div class="text-sm text-gray-500">
                        {(() => {
                            if (user.roles.has(userRoles.EMPLOYEE))
                                return "Сотрудник"
                            else
                                return "Вход в систему не выполнен"
                        })()}
                    </div> */}
                    </div>
                    <TimeoutButtonOutline timeout={20} onClick={(e) => {this.onLogout.call(this)}}>
                        выход
                    </TimeoutButtonOutline>
                    </div>
                    </div>
                )
            else return "";
        }
    }
}