import React from 'react';
import PropTypes from 'prop-types'

export default class ModalBackground extends React.Component {
    static propTypes = {
        /**
         *  Действие при закрытии окна
         *  @default (e) => {e.preventDefault()}
         */
        onClose: PropTypes.func,
    }

    static defaultProps = {
        onClose: (e) => {e.preventDefault()},
    }

    constructor(props) {
        super(props);
        this.background = React.createRef();
    }

    render() {
        return (
            <div ref={this.background}
            class="fixed top-0 left-0 w-full h-full bg-gray-900 bg-opacity-50 flex items-center justify-center"
            onClick={(e) => {
                if (e.target == this.background.current)
                    this.props.onClose(e)
            }}>
            {this.props.children}
            </div>
        );
    }
}