import React from 'react';
import Lightbox from "react-awesome-lightbox";
import LoaderScreen from "../LoaderScreen"
import { getMaps } from '../../scripts/api/contentApi';
// You need to import the CSS only once
import "react-awesome-lightbox/build/style.css";
import "../../assets/css/lightbox-custom.css"

export default class MapDetail extends React.Component {
    constructor(props) {
        super(props);
        this.state = {imageList: [], loading: true}
    }

    async componentDidMount() {
        /*
        * Тут в будущем можно будет написать запрос к апи
        * или ещё к чему
        */
        

        // Это для симуляции загрузки
        const response = await getMaps(this.props.category)

        this.setState({ imageList: response, loading: false });
    }

    render() {
        console.log(this.state.imageList);
        if (this.state.loading) {
            return (
                <LoaderScreen/>
            );
        }
        else {
            return (
                <Lightbox images={this.state.imageList} allowRotate={false} doubleClickZoom={2}
                onClose={() => {this.props.history.push('/maps')}}/>
            );
        }
    }
}
