import ToolbarSecondary from '../Toolbars/ToolbarSecondary';
import React from 'react';

const getGoBackPath = (component) => {
    let currentPath = component.props.location.pathname;

    if (currentPath.lastIndexOf("/") === currentPath.length - 1)
        currentPath = currentPath.substr(0, currentPath.length - 1)

    return currentPath.substr(0, currentPath.lastIndexOf("/") + 1)
}

export class ServicesGoBack extends React.Component {
    render() {
        return (
        <div class="container fixed bottom-10 mx-auto">
            <ToolbarSecondary size="lg" buttons={[
                {
                    text: "Назад",
                    onClick: ((e) => {
                        e.preventDefault();
                        this.props.history?.push(getGoBackPath(this))
                    }).bind(this),
                    //timeout: 100,
                },
            ]}
            />
        </div>
    )
    }
}