import React from 'react';
import MainButton from '../../Buttons/MainButton';
import PropTypes from 'prop-types'
import '../../../assets/css/postcard.css'
import InputMask from 'react-input-mask';
import format from '../../../scripts/helperFunctions/format';
import VirtualPinpad from '../../VirtualPinpad'

export default class InputPhoneForm extends React.Component {
    static propTypes = {
        /**
         *  Действие при отправки формы
         *  @default (e) => {e.preventDefault()}
         */
        onSubmit: PropTypes.func,

        /**
         *  Действие при изменении значения поля
         *  @default (value, mask) => console.log(value)
         */
         onChange: PropTypes.func,
        
        /**
         *  Значение формы
         */
         phoneNumber: PropTypes.oneOfType(PropTypes.string, PropTypes.number)
    }

    static defaultProps = {
        onSubmit: (data) => {},
        onChange: (value, mask) => console.log(value),
    }

    constructor(props) {
        super(props);
        console.log(props.phoneNumber)
        this.state = {phoneNumber: props.phoneNumber}
    }

    onPhoneChange = (event) => {
        let number = event.target.value.replace(/[ _()-]/g, '');
        console.log(number)
        this.setState({
            phoneNumber: number
        });
    }

    componentDidMount() {
        this.searchInput.focus();
    }
    
    render() {
        // TODO:
        // Это костыль на первое время
        // Переписать обязательно
        //let redirectTo = this.props.location.search
        //.substr(this.props.location.search.indexOf('redirectTo=') + 'redirectTo='.length)
        return (
            <>
            <div className="mx-auto text-center relative max-w-5xl">
            <div class="w-full">
            
            <form className="bg-white mt-10 shadow-md rounded px-8 mb-4">
                <div class="absolute -top-5 -left-5 -right-5 -bottom-5 -z-10 bg-postcard rounded shadow-md"></div>
                <h1 className="text-5xl pt-12 mx-auto text-blue-darker font-black">
                        Войти как сотрудник
                </h1>
                <p className="text-2xl py-2 mx-auto text-gray-500">
                    Введите ваш номер телефона в поле ниже и нажмите на кнопку "Продолжить". <br/>
                    Мы вышлем код подтверждения на указанный номер
                </p>
                <div className="my-8">
                <div className="grid h-24 grid-cols-2 grid-rows-1 gap-4 my-2">
                    <div><InputMask 
                    inputRef={inputEl => (this.searchInput = inputEl)}
                    mask="+7 (999) 999-99-99"
                    maskChar={'_'}
                    value={this.props.phoneNumber}
                    onChange={this.onPhoneChange} 
                    //beforeMaskedValueChange={this.beforeMaskedValueChange}

                    className="shadow appearance-none border rounded w-full h-full py-2 px-3
                        text-gray-500 text-4xl font-black leading-tight tracking-widest focus:outline-none focus:shadow-outline"
                    id="username" type="tel"
                    /></div>

                    <MainButton
                    className="bg-red hover:bg-blue-lighter" size="lg" direction="right"
                    onClick={(e) => {
                        e.preventDefault();
                        console.log(this.state.phoneNumber)
                        this.props.onSubmit({phoneNumber: this.state.phoneNumber})
                    }}
                    >
                        Продолжить
                    </MainButton>
                </div>
                <p className="text-center mt-6 text-gray-500 text-lg">
                &copy; 2021 РМ Рейл <br/>
                </p>
                </div>
            </form>
            
            </div>
            </div>
            <VirtualPinpad onInput={(e) => this.searchInput.value += e}/>
            </>
        );
    }
}