import React from 'react';
import PropTypes from 'prop-types'

import {
    // BrowserRouter as Router,
    Switch,
    Route,
    Redirect,
  }
from 'react-router-dom';

import AskLoginForm from './Login/AskLoginForm';
import InputPhoneForm from './Login/InputPhoneForm';
import ConfirmForm from './Login/ConfirmNumberForm';
import LoaderScreen from '../LoaderScreen'
import ReadCardForm from './Login/ReadCardForm';

import reducer from '../../scripts/reducer'

import { userRoles } from '../../scripts/user/User';
import {Session} from '../../scripts/user/Session'
import { ACTION } from '../../scripts/constants/dummyConstants';

import ToolbarMain from '../Toolbars/ToolbarMain';
import ToolbarSecondary from '../Toolbars/ToolbarSecondary';
import FailReport from '../FailReport';
import BaseForm from './BaseForm';

import ServiceAfterSuccess from './info/ServiceAfterSuccess';
import ServiceAfterFail from './info/ServiceAfterFail';

// на экране "подтвердите номер", маска номера подтверждения
// изменить только в случае смены длины номера, это важно
const confirmNumberMask = '99999';
export default class Forms extends React.Component {

    // Дописать действия при отправке формы (props.onSubmit)

    constructor(props) {
        super(props);
        this.state = {loading: true}
    }

    componentDidMount() {
        this.setState({session: Session.getCurrent(), loading: false})

        const search = this.props.location.search;
        const success = new URLSearchParams(search).get("success") || '/';
        const fail = new URLSearchParams(search).get("fail") || '/';

        this.onSuccess = (callback) => {
            this.props.history.push(success)
            callback?.();
        }

        this.onFail = (callback) => {
            this.props.history.push(fail)
            callback?.()
        }

        //console.log(success)
        //console.log(fail)
        
    }

    onSuccess = (callback) => {
        this.props.history.push('/')
    }

    onFail = (callback) => {
        this.props.history.push('/')
    }

    componentWillMount() {
        
    }

    setLoading(value, callback) {
        this.setState({loading: value}, callback?.())
    }

    updateSession(result, callback = () => {}) {
        this.setState({session: Session.getCurrent(), result: result}, callback)
    }

    onCardSubmit(data) {
        let setLoading = this.setLoading.bind(this),
        updateSession = this.updateSession.bind(this);
        let onSuccess = this.onSuccess.bind(this);
        let onFail = this.onFail.bind(this)
        setLoading(true)
        
        return new Promise(function(resolve, reject) {
            reducer({type: ACTION.READ_CARD, data: data}).then((result) => {
                updateSession(undefined, setLoading(false, resolve(result)));
                // WARNING
                onSuccess()
                resolve(result)
            },
            )
            .catch(result => {
                updateSession(<FailReport>
                    Неверный номер карты
                </FailReport>, setLoading(false, reject(result)));
                reject(result)
            })
        })
    }

    onPhoneSubmit(data) {
        let setLoading = this.setLoading.bind(this),
        updateSession = this.updateSession.bind(this);

        setLoading(true)
        
        return new Promise(function(resolve, reject) {
            reducer({type: ACTION.LOGIN, data: data}).then((result) => {
                updateSession(undefined, setLoading(false, resolve(result)));
                // ...
            },
            )
            .catch(result => {
                updateSession(<FailReport>
                    Неверный номер телефона
                </FailReport>, setLoading(false, reject(result)));
                // ...
                reject(result)
            })
        })
    }

    
    // TODO: выставить confirmNumberChange наружу
    onConfirmNumberChange = (event) => {
        let confirmNumber = event.target.value.replace(/[ _()-]/g, '');

        this.setState({
            confirmNumber: confirmNumber,
        });

        console.log(confirmNumber);

        if (confirmNumber.length === confirmNumberMask.length) {
            this.onConfirmNumberSubmit({phoneNumber: this.state.session.user.phoneNumber, confirmNumber: confirmNumber})
        }
    }

    onConfirmNumberSubmit(data) {
        let setLoading = this.setLoading.bind(this),
        updateSession = this.updateSession.bind(this);

        let onSuccess = this.onSuccess.bind(this);
        let onFail = this.onFail.bind(this);

        setLoading(true)
        
        return new Promise(function(resolve, reject) {
            reducer({type: ACTION.CONFIRM, data: data}).then((result) => {
                updateSession(undefined, setLoading(false, resolve(result)));
                // ...
                onSuccess();
                resolve(result)
            },
            )
            .catch(result => {
                updateSession(<FailReport>
                        Введён неверный номер подтверждения
                    </FailReport>, setLoading(false, reject(result)));
                // ...
                reject(result)
            })
        })
    }

    onLogout(data) {
        let setLoading = this.setLoading.bind(this),
        updateSession = this.updateSession.bind(this);

        setLoading(true)
        
        return new Promise(function(resolve, reject) {
            reducer({type: ACTION.LOGOUT, data: data}).then((result) => {
                updateSession(undefined, setLoading(false, resolve(result)));
            },
            )
            .catch(result => {
                updateSession(<FailReport>Произошла ошибка</FailReport>,
                     setLoading(false, reject(result)));
            })
        })
    }

    onServiceSubmit(data) {
        const setLoading = this.setLoading.bind(this),
        updateSession = this.updateSession.bind(this);

        const onSuccess = this.onSuccess.bind(this),
        onFail = this.onFail.bind(this),
        onLogout = this.onLogout.bind(this);


        const lifeReport = <ServiceAfterSuccess
            user={this.state.session.user}
            serviceText={data.serviceId}
            onContinue = {(e) => {onSuccess()}}
            onExit = {() => {
                let logout = onLogout();
                logout.then(setTimeout(this.props.history.push('/'), 1000))
                //.then(result => onSuccess())
                //.catch(result => onSuccess())
            }}
            />

        const deathReport = <ServiceAfterFail
            user={this.state.session.user}
            serviceText={data.serviceId}
            onContinue = {(e) => {updateSession(undefined, setLoading(false))}}
            onExit = {(e) => {
                onFail()
            }}
            />

        // Запускаем загрузку
        setLoading(true)
        
        return new Promise(function(resolve, reject) {
            reducer({type: ACTION.REQSERVICE, data: data}).then((result) => {
                updateSession(lifeReport, setLoading(false));
                // ...
            },
            )
            .catch(error => {
                updateSession(deathReport, setLoading(false));
                // ...
            })
        })
    }
    

    render() {
        

        if (this.state.loading)
            return <LoaderScreen/>
        else {
            const user = this.state.session.user;
            console.log(this.state.session);
            //console.log(Session.getCurrent());
        return (
            <Switch>
            <Route path='/forms/ask-login' 
            render={(props) => (
            <div className="container mx-auto text-center relative h-screen">
            <AskLoginForm {...props}/>
            <ToolbarMain buttons={[
                {
                    text: "Продолжить как гость",
                    onClick: (e) => {
                        e.preventDefault();
                        this.onFail.call(this)
                    }
                },
                {
                    text: "Войти в систему",
                    onClick: (e) => {
                        e.preventDefault();
                        (() => this.props.history.push('/forms/login')).call(this);
                    }
                },
            ]}
            />
            </div>
            ) }
            />
            <Route path='/forms/login' render={(props) => (
                user.roles.has(userRoles.UNCONFIRMED)
                ?
                <div className="container mx-auto text-center relative max-w-5xl"> 
                    {(() => {
                        if (this.state.result) {
                            return <h6 className="mt-5 text-3xl text-red">{this.state.result}</h6>
                        }
                    })()}
                
                    <ConfirmForm phoneNumber={user.phoneNumber}
                    onChange={this.onConfirmNumberChange.bind(this)}
                    numberMask = {confirmNumberMask}
                    {...props}/>
                    <div className="mt-16 -mx-5">
                    <ToolbarSecondary size="lg" buttons={[
                        {
                            text: "Отмена",
                            onClick: (e) => {
                                e.preventDefault();
                                this.onFail.call(this);
                            },
                            timeout: 60,
                        },
                        {
                            text: "Отправить номер повторно",
                            onClick: (e) => {
                                e.preventDefault();
                                this.onPhoneSubmit.call(this, {phoneNumber: user.phoneNumber})
                            },
                            color: 'blue-darker'
                        },
                        {
                            text: "Ввести другой номер",
                            onClick: (e) => {
                                e.preventDefault();
                                this.onLogout.call(this)
                            },
                            color: 'blue-darker'
                        },
                    ]}
                    />
                    </div>
                </div>
                : user.roles.has(userRoles.INPUT_PHONE)

                    ? <>
                    <div className="container mx-auto text-center relative max-w-5xl">
                    {(() => {
                        if (this.state.result) {
                            return <h6 className="text-3xl mt-5 text-red">{this.state.result}</h6>
                        }
                    })()}
                    <InputPhoneForm
                    onSubmit = {this.onPhoneSubmit.bind(this)}
                    phoneNumber = {user?.phoneNumber}
                    {...props}/>
                    <div className="mt-16 -mx-5">
                    <ToolbarSecondary size="lg" buttons={[
                        {
                            text: "Отмена",
                            onClick: (e) => {
                                e.preventDefault();
                                this.onFail.call(this);
                            },
                            timeout: undefined,
                        },
                    ]}/>
                    </div>
                    </div>
                    </>

                    : <>
                    <div className="container mx-auto text-center relative max-w-5xl">
                    {(() => {
                        if (this.state.result) {
                            return <h6 className="text-3xl mt-5 text-red">{this.state.result}</h6>
                        }
                    })()}
                    </div>

                    <ReadCardForm onSubmit={this.onCardSubmit.bind(this)}
                    onCancel={this.onFail.bind(this)}
                    />
                    </>
                )} />

            {
                /**
                 * Логика работы формы с отправкой услуги
                 * this.state.result содержит в себе компонент
                 */
            }
            <Route path='/forms/service/:id' render={(props) => {
                const serviceId = props.match.params.id;
                
                if (this.state.result) {
                    console.log(this.state.result);
                    return this.state.result;
                }

                else {
                    return (
                    <div class="max-w-3xl mx-auto">
                    <BaseForm
                    serviceText={serviceId}
                    name={user.name}
                    phone={user.phoneNumber}
                    onSubmit={((e) => {
                        e.preventDefault();
                        this.onServiceSubmit.call(this, {serviceId})
                    }).bind(this)}
                    {...props}/>
                    
                    <div class="-mx-5 my-8">
                    <ToolbarSecondary size="lg" buttons={[
                        {
                            text: "Отмена",
                            onClick: (e) => {
                                e.preventDefault();
                                this.onFail.call(this);
                            },
                            //timeout: 60,
                        },
                        {
                            text: "Другой пользователь",
                            onClick: (e) => {
                                e.preventDefault();
                                this.onLogout.call(this)
                                .then((() => this.props.history.push(
                                    `/forms/login?success=/forms/service/${serviceId}&fail=/forms/service/${serviceId}`
                                )).call(this))
                            },
                            color: 'blue-darker'
                        },
                    ]}/>
                    </div>
                    </div>
                    )
                }
            }}/>
            <Redirect to="/" />
            </Switch>
        )
        }
    }
}