export const inputCard = (session, data) => {
    return new Promise(function(resolve, reject) {
        // Симулируем какой-то процесс обращения к апи с помошью setTimeout
        if (data.card === DUMMY_CARD_NUMBER) {
            
            setTimeout(() => {
                Session.startNew({
                    user: new User({...session.user, name: DUMMY_USER_NAME, phoneNumber: DUMMY_PHONE,
                        roles: session.user.roles.add(userRoles.EMPLOYEE),
                        //roles: session.user.roles.add(userRoles.INPUT_PHONE),
                    }),
                    // expires: 600,
                });
                resolve(status.SUCCESS);
            }, Math.random() * 1000 + 250)
        }
        else {
            setTimeout(() => {
                reject(status.FAIL);
            }, Math.random() * 1000 + 250)
        }
    });
}

export const inputNumber = (session, data) => {
    return new Promise(function(resolve, reject) {
        // Симулируем какой-то процесс обращения к апи с помошью setTimeout
        if (data.phoneNumber === DUMMY_PHONE) {
            
            setTimeout(() => {
                Session.startNew({
                    user: new User({...session.user, phoneNumber: DUMMY_PHONE,
                        roles: session.user.roles.add(userRoles.UNCONFIRMED)}),
                    // expires: 600,
                    authToken: DUMMY_TOKEN,
                });
                resolve(status.SUCCESS);
            }, Math.random() * 1000 + 250)
        }
        else {
            setTimeout(() => {
                reject(status.FAIL);
            }, Math.random() * 1000 + 250)
        }
    });
}

export const confirmNumber = (session, data) => {
    return new Promise(function(resolve, reject) {
        // Симулируем какой-то процесс обращения к апи с помошью setTimeout
        if (data.phoneNumber === DUMMY_PHONE
            && data.confirmNumber === DUMMY_CONFIRM_NUMBER) {
            
            setTimeout(() => {
                Session.startNew({
                    user: new User({...session.user, name: DUMMY_USER_NAME,
                    roles: new Set([userRoles.GUEST, userRoles.EMPLOYEE])}),

                    authToken: DUMMY_TOKEN,
                    // expires: 600,
                });
                resolve(status.SUCCESS);
            }, Math.random() * 1000 + 250)
        }
        else {
            setTimeout(() => {
                reject(status.FAIL);
            }, Math.random() * 1000 + 250)
        }
    });
}

export const logout = (session, data = {}) => {
    return new Promise(function(resolve, reject) {
        setTimeout(() => {
            Session.startNew();
            resolve(status.SUCCESS)
        }, Math.random() * 1000 + 250)
    });
}

export const requestService = (session, data = {}) => {
    // data.serviceId - id запрашиваемой услуги
    return new Promise(function(resolve, reject) {
        if (session.authToken === DUMMY_TOKEN) {
            setTimeout(() => {
                resolve(status.SUCCESS)
            }, Math.random() * 1000 + 250)
        }
        else {
            setTimeout(() => {
                reject(status.FAIL)
            }, Math.random() * 1000 + 250)
        }
    });
}