import React from 'react';
import PropTypes from 'prop-types'

const sizes = {
    'sm': {
        height: "h-12",
        width: "w-12",
        border: "border-2",
        padding: "px-8",
        text: 'text-md',
    },
    'lg': {
        height: "h-16",
        width: "w-16",
        border: "border-2",
        padding: "px-12",
        text: 'text-xl',
    }
}


export default class TimeoutButtonOutline extends React.Component {
    
    static propTypes = {
        /**
         *  Действие при нажатии кнопки либо по истечении времени
         *  @default () => {}
         */
        onClick: PropTypes.func,

        /**
         *  Время в секундах до осуществления действия
         *  @default undefined
         */
        timeout: PropTypes.number,

        /**
         * Размер кнопки. Один из ['sm', 'lg']
         * @default 'sm'
         */
        size: PropTypes.string,

        /**
         * Цвет кнопки. СТРОКА. См. цветообразование в tailwind css \
         * Также в проект зашиты другие цвета в tailwind.config.js
         * 
         * @default 'red'
         */
        color: PropTypes.string,

        /**
         * Контрастный цвет к заданному (color) СТРОКА. См. цветообразование в tailwind css \
         * Также в проект зашиты другие цвета в tailwind.config.js \
         * 
         * @default 'white'
         */
        contrastColor: PropTypes.string,
    }

    static defaultProps = {
        onClick: () => {},
        timeout: undefined,
        size: 'sm',
        color: 'red',
        contrastColor: 'white'
    }

    constructor(props) {
        super(props);
        this.state = {timer: props.timeout}
    }

    componentDidMount() {
        if (this.state.timer)
            this.onTimerTick.call(this);
    }

    onTimerTick() {
        if (this.state.timer <= 0) {
            this.props.onClick({preventDefault: () => {}});
        }
        else {
            setTimeout(() => {
                this.setState({timer: this.state.timer - 1},
                    this.onTimerTick.call(this));
            }, 1000)
        }
    }

    render() {
        const size = sizes[this.props.size] || sizes['sm'];
        const color = {
            text: `text-${this.props.color}`,
            border: `border-${this.props.color}`,
            hoverText: `hover:text-${this.props.contrastColor}`,
            hoverBg: `hover:bg-${this.props.color}`,
        }

        /*         
            h-12
            flex items-center
            text-red
            justify-center uppercase
            font-semibold
            border-red
            undefined
            border-red hover:bg-red
            hover:text-white
            transition duration-500
            ease-in-out 
            text-md"
         */

        return (
        <button class="inline-flex" onClick={this.props.onClick}>
            <span class={`
            ${size.height}
            flex items-center
            ${color.text}
            justify-center uppercase
            font-semibold
            ${color.border}
            ${size.padding}
            ${size.border}
            ${color.border}
            ${color.hoverBg}
            ${color.hoverText}
            transition duration-500
            ease-in-out 
            ${size.text}`}>
                {this.props.children}
            </span>
            {(() => {
                if (typeof this.props.timeout !== 'undefined') {
                    return <span class={`
                    ${size.height}
                    ${size.width} 
                    flex-shrink-0
                    flex ${color.text}
                    items-center justify-center 
                    ${size.border}
                    border-l-0 font-black
                    ${color.border} ${color.hoverBg}
                    ${color.hoverText}
                    transition duration-500
                    ease-in-out
                    ${size.text}
                    `}>
                            {this.state.timer}
                        </span>
                }
            })()}
        </button>
        )
    }
}