import React from 'react';
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom';

/**
 * Кнопка на главной странице
 */
export default class MainButton extends React.Component {
    static propTypes = {
        /** 
         * Иконка кнопки, url
         */
        icon: PropTypes.string,

        /** 
         * Ссылка, по которой ведёт кнопка \
         * Если задана, кнопка оборачивается в ссылку и функция OnClick не срабатывает \
         */
        link: PropTypes.string,

        /** 
         * Действие при нажатии кнопки
         */
        onClick: PropTypes.func,

        /** 
         * Добавочные css классы к кнопке \
         * желательно использовать классы Tailwind Css
         * @example className = "col-span-2 row-span-1" 
         * растянет кнопку внутри сетки (CSS) на 1 ряд и 2 колонки 
         * @example className = "bg-blue-lighter hover:bg-red"
         * сделает кнопку синей, а при наведении на неё мышкой - красной 
         */
        className: PropTypes.string,

        /** 
         * Внутри компонента пишется текст кнопки
         */
        children: PropTypes.oneOfType([PropTypes.number, PropTypes.string])
    }

    static defaultProps = {
        icon: undefined,
        children: 'Кнопка',
    }
    
    render() {
        const ParentTagName = this.props.link ? Link : 'button'
      return (
        <ParentTagName
        className={`relative bg-red transition-colors duration-200 rounded flex
        justify-center items-center text-white shadow-xl
        ` + this.props.className}
        to={this.props.link ? this.props.link : undefined}
        onClick={this.props.link ? undefined : this.props.onClick}>
            <div className="text-center my-4">
                {(() => {if (this.props.icon) {
                    return (
                    <div className="w-28 h-28 p-1 mx-auto">
                            <img className="min-w-full" src={this.props.icon} alt="icon" />
                    </div>)
                }})()}

                <h3 className="text-4xl font-black">
                    {this.props.children}
                </h3>
            </div>
        </ParentTagName>
      );
    }
}