import React from 'react';
import MainButton from '../Buttons/MainButton'
import SecondaryButton from '../Buttons/SecondaryButton'
import ContentButton from '../Buttons/ContentButton'
import { func } from 'prop-types';
import { getMenu } from '../../scripts/api/contentApi';
import FailReport from '../FailReport';
import { ServicesGoBack } from './GoBackComponents';

const switches = {
    'MAIN': {
        Component: MainButton,
        className: () => "col-span-1",
        GoBackComponent: undefined
    },
    'TWO_SIDES': {
        Component: SecondaryButton,
        className: () => "col-span-1",
        direction: (index) => (index % 2 == 0) ? 'left' : 'right',
        GoBackComponent: ServicesGoBack
    }
}

export default class AutoMenu extends React.Component {

    constructor(props) {
        super(props);
        this.state = {loading: true}
    }

    componentDidUpdate(prevProps) {
        if (prevProps.url != this.props.url) {
            this.componentDidMount();
        }
    }

    componentDidMount() {
        getMenu(this.props.url).then((result => {
            console.log(result)
            this.setState(({loading: false, ...result}))
        }).bind(this))
        .catch((error => {
            this.setState({loading: false, error: true})
        }).bind(this))
    }

    render() {
        const e = switches[this.state.type]

        return (
            this.state.loading ?
            "Идёт загрузка меню..."
            : this.state.error ?
            <FailReport>
                Произошла ошибка при загрузке меню!
            </FailReport>
            : <>
            {
            this.state.buttons.map((button, index) => 
                <e.Component
                className={e.className?.(index)}
                size={e.size?.(index)}
                direction={e.direction?.(index)}
                description={button.detail}
                icon={button.icon}
                link={button.link}
                onClick={button.link? undefined : button.onClick}
                >
                    {button.text}
                </e.Component>
            )
            }
            {
                this.state.goBack && e.GoBackComponent ?
                    <e.GoBackComponent {...this.props}/>
                : null
            }
            </>
        )
    }
}