import logo from './logo.svg';
import MainPage from './components/MainPage/MainPage';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from 'react-router-dom';

import ServicesRoutes from './components/Services/Services';
import Forms from './components/Forms/UserForms';
import Maps from './components/Maps/Maps';
import News from './components/News/News';
import { Session } from './scripts/user/Session';

function App() {
  // Входим в систему как гость, если не находим сессии
    if (typeof Session.getCurrent() === 'undefined' || Object.keys(Session.getCurrent()).length === 0) {
        Session.startNew()
    }

    //console.log(Session.getCurrent())
  
    return (
        <Router>
        <Switch>
        {/*<Route path="/services" component={Services}/>*/}
        <Route path="/forms" render={(props) => <Forms {...props}/> } />
        <Route path="/services" render={(props) => <ServicesRoutes {...props}/> } />
        <Route path="/maps" render={(props) => <Maps {...props}/> } />
        <Route path="/news" render={(props) => <News {...props}/> } />
        <Route path="/" render={(props) => <MainPage {...props}/> } />    
        <Redirect to="/" />
        </Switch>
        </Router>
    );
}

export default App;
