import React from 'react';
import ServicesMenu from './ServicesMenu';
import { Session } from '../../scripts/user/Session';
import { userRoles } from '../../scripts/user/User';

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect,
  }
from 'react-router-dom';

import Finance from './Finance';
import BaseForm from '../Forms/BaseForm'
import AutoMenu from '../Menus/AutoMenu';

export default class ServicesRoutes extends React.Component {

    constructor(props) {
        super(props);
        this.state = {loading: true, url: props.location.pathname}
    }

    componentDidMount() {
        const search = this.props.location.search;
        this.allowGuest = ((new URLSearchParams(search).get("guest")) === 'true') || false;

        this.setState({session: Session.getCurrent(), loading: false}, () => {
            if (!this.state.session.user.roles.has(userRoles.EMPLOYEE) && !this.allowGuest) {
                this.props.history.push('/forms/ask-login?success=/services&fail=/services?guest=true');
            }
        });
    }

    componentDidUpdate(prevProps) {
        if (this.props.location.pathname != prevProps.location.pathname) {
            this.setState({url: this.props.location.pathname})
        }
    }

    render() {
        
        return (
            <Switch>
            {/*<Route path="/services" component={Services}/>*/}
            
            <Route path='/services' render={(props) =>
                <>
                <div className="container mx-auto text-center">
        
                    {/* Контейнер с кнопками */}
                    {console.log(this.state.url)}
                    <div className="grid grid-cols-2 gap-4 py-10 px-4">
                    <AutoMenu url={this.state.url} {...props}/>
                    </div>
                </div>
                </>
            }/>

            <Redirect to="/" />
            </Switch>
        )
    }
}