import React from 'react';
import SecondaryButton from '../Buttons/SecondaryButton';
import PropTypes from 'prop-types'
import ToolbarSecondary from '../Toolbars/ToolbarSecondary';

export default class ServicesMenu extends React.Component {
    static propTypes = {
        /**
         *  Нужно ли выводить кнопку "назад"
         *  @default true
         */
        goBack: PropTypes.bool,

        /** 
         * Текст кнопки "назад"
         * Не выводится, если goBack = false
         * @default "Назад"
         */
        goBackText: PropTypes.string,

        /** 
         * Массив кнопок, которые нужно отобразить.
         * *Внимательно посмотрите на пример в коде компонента*
         * @default []
         * @example []
         */
        buttons: PropTypes.array,
    }

    static defaultProps = {
        goBack: true,
        goBackText: 'Назад',
        buttons: [],
    }

    getGoBackPath() {
        let currentPath = this.props.location.pathname;

        if (currentPath.lastIndexOf("/") === currentPath.length - 1)
            currentPath = currentPath.substr(0, currentPath.length - 1)

        return currentPath.substr(0, currentPath.lastIndexOf("/") + 1)
    }

    render() {
        let buttons = [...this.props.buttons]

        /*
        Кнопку "назад" решили поцепить другим способом
        if (this.props.goBack) {
            buttons.unshift (
                {
                    icon: `<svg fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 6h16M4 12h16M4 18h16"></path>
                    </svg>`,
                    link: this.getGoBackPath(),
                    onClick: () => {},
                    description: '',
                    text: this.props.goBackText,
                }
            )
        }
        */

      return (
      <div>
        <div className="container mx-auto text-center">

            {/* Контейнер с кнопками */}
            <div className="grid grid-cols-2 gap-4 py-10 px-4">
                {
                    buttons.map((button,index)=> {
                        return (
                            <SecondaryButton className="col-span-1" direction={index % 2 === 0 ? 'left' : 'right'}
                            icon={button.icon}
                            link={button.link}
                            description={button.description}
                            >
                                {button.text}
                            </SecondaryButton>
                        )
                    })
                }
            </div>
            {(() => {
                if (this.props.goBack)
                    return (
                        <div class="mx-4">
                            <ToolbarSecondary size="lg" buttons={[
                                {
                                    text: "Назад",
                                    onClick: ((e) => {
                                        e.preventDefault();
                                        this.props.history.push(this.getGoBackPath())
                                    }).bind(this),
                                    //timeout: 100,
                                },
                            ]}
                            />
                        </div>
                    );
            })()}
        </div>
        </div>
      );
    }
}