import React from 'react';
import PropTypes from 'prop-types'

import FailReport from '../../FailReport'
import format from '../../../scripts/helperFunctions/format';
import ToolbarSecondary from '../../Toolbars/ToolbarSecondary';

export default class ServiceAfterFail extends React.Component {

    // TODO: подключить эту штуку в forms
    // TODO: сделать похожие формы при ошибке, а также после входа в систему

    static propTypes = {
        /**
         *  Объект "класса" user
         */
        user: PropTypes.object,

        /**
         * Название услуги, которую мы только что отправили
         */
        serviceText: PropTypes.string,

        /** 
         * Действие при продолжении сессии (нажали на кнопку "Попробовать ещё раз")
         */
        onContinue: PropTypes.func,

        /** 
         * Действие при выходе (нажали на кнопку "Завершить сеанс")
         */
        onExit: PropTypes.func,
    }

    /** 
     * ОБЯЗАТЕЛЬНО ЗАПОЛНИТЕ ВСЕ ПРОПЫ!!!
     */
    static defaultProps = {

    }

    render() {
        return (
            <div class="container mt-32 mx-auto">
                <FailReport>
                    Мы не смогли отправить заявку <br/>
                    "{this.props.serviceText}" <br/>
                    на имя {" " + this.props.user.getFullName().firstName}
                    {" " + this.props.user.getFullName().lastName}
                </FailReport>
                <div class="max-w-5xl mx-auto mt-8">
                    <ToolbarSecondary buttons={[
                        {
                            text: "Попробовать ещё раз",
                            onClick: (e) => this.props.onContinue(),
                            color: 'blue-darker'
                        },
                        {
                            text: "На главный экран",
                            onClick: (e) => this.props.onExit(),
                            timeout: 60,
                            //color: 'blue-darker'
                        },
                    ]}
                    />
                </div>
            </div>
        );
    }
}